body {
  margin: 0;
  margin-top: 15px;
}

table,
input,
label {
  font-size: 1.2rem;
  font-family: monospace;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 1.5rem;
  font-family: monospace;
}

.subTitle {
  font-weight: bold;
}

.checkbox {
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-around;
}

span {
  display: block;
  font-size: 1.2rem;
  font-family: monospace;
  margin-bottom: 0.5rem;
}

table {
  width: 100%;
}

td {
  padding: 0.3rem;
}

th {
  padding-right: 1rem;
  text-align: left;
}

tr:nth-child(even) {
  background: #eee;
}

input[type='number'] {
  display: block;
}

.container {
  max-width: 350px;
  margin: 0 auto;
}

section {
  text-align: -webkit-center;
  padding: 0 15px;
}

.buttons {
  margin: 0;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-evenly;
}

button {
  color: #eee;
  background: rgba(0, 0, 0, 1);
  border: none;
  padding: 0.5rem 1.4rem;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  transition: 0.5s;
  background: rgba(0, 0, 0, 0.5);
}
